.Animate-Border-Glow {
    animation-name: border-glow;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    transition-duration: 0.1s;
    cursor: pointer;
}

.Animate-Border-Glow:hover {
    background-color: rgb(251, 230, 230) !important;
}

.Animate-Border-Glow:active {
    border: 3px solid black !important;
    background-color: rgb(221, 219, 219) !important;
}

@keyframes border-glow {
    0% { box-shadow: 0px 0px 10px red; border: 3px solid rgba(255, 0, 0, 0); transform: rotate(0deg); }
    30% { transform: rotate(0deg); }
    35% { transform: rotate(0.2deg); }
    45% { transform: rotate(-0.2deg); }
    55% { transform: rotate(0.2deg); }
    65% { transform: rotate(-0.2deg); }
    70% { transform: rotate(0deg); }
    100% { box-shadow: 0px 0px 10px rgba(255, 0, 0, 0.243); border: 3px solid rgb(0, 0, 0); transform: rotate(0deg); }
}

.Click-Blocker {
    display: flex;
